<template>
    <div class="login-wrap">
    
        <div class="ms-login">
    
            <div class="ms-title">GO HOME</div>
    
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="ms-content">
    
                <el-form-item prop="username">
    
                    <el-input v-model="ruleForm.username" placeholder="请输入用户名">
    
                        <el-button slot="prepend" icon="el-icon-lx-people"></el-button>
    
                    </el-input>
    
                </el-form-item>
    
                <el-form-item prop="password">
    
                    <el-input type="password" placeholder="请输入密码" v-model="ruleForm.password">
    
                        <el-button slot="prepend" icon="el-icon-lx-lock"></el-button>
    
                    </el-input>
    
                </el-form-item>
    
                <div class="login-btn">
    
                    <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
    
                </div>
    
            </el-form>
    
        </div>
    
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            authCodeImg:null,
            ruleForm: {
                username: "",
                password: "",
            },
            rules: {
                username: [{ required: true, message: "请输入用户名", trigger: "blur" }],
                password: [{ required: true, message: "请输入密码", trigger: "blur" }],
            }
        };
    },
    created(){
        
        //this.getCaptcha()
    },
    methods: {
        //获取验证码
        async getCaptcha() {
            const data = await this.$api.Captcha();
            this.authCodeImg = "data:image/png;base64," + data;
        },
        //登录校验
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.Login()
                } else {
                    return false;
                }
            });
        },
        //login
        async Login() {
            const data = await this.$api.UserLogin(this.ruleForm);
            console.log(data)
            if(!data){
                //this.getCaptcha()
                return
            }
            this.$cookie.setCookie('userId',data.data)
            console.log(this.$cookie.getCookie('userId'))
            let path = "/missingperson";
            //let path = '/bannerAdd'
            //if (this.$route.query.redirect) {
                // URL Encode
               // path = decodeURIComponent(this.$route.query.redirect);
            //}
            this.$router.push({path});
        },
    }
};
</script>

<style>

</style>

<style scoped>
.login-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    background-image: url(../assets/img/login-bg.jpg);
    background-size: 100%;
}

.ms-title {
    width: 100%;
    line-height: 50px;
    text-align: center;
    font-size: 20px;
    color: #fff;
    border-bottom: 1px solid #ddd;
}

.ms-login {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 350px;
    margin: -190px 0 0 -175px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.3);
    overflow: hidden;
}

.ms-content {
    padding: 30px 30px;
}

.login-btn {
    text-align: center;
}

.login-btn button {
    width: 100%;
    height: 36px;
    margin-bottom: 10px;
}

.authCode-wrap{
    position: relative;
    overflow: hidden;
}
.authCode-img{
    position: absolute;
    top: -4px;
    right: 0;     
}
</style>
